.record {
    width: 100%;
    height: 100%;
    padding: 20px;
    color: #000;
}

.record>h2 {
    color: #000;
    font-size: 25px;
    height: 0.3rem;
    line-height: 0.3rem;
}

.up {
    width: 100%;
    height: 2rem;
    display: flex;
    background: #fff;
    overflow: hidden;
}

.up>.upLeft {
    width: 60%;
    height: 100%;
    background: #fff;
    overflow: hidden;
}

.up>.upLeft>ul {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: center;

}

.up>.upLeft>ul>li {
    width: 50%
}

.up>.upLeft>ul>li>span {
    width: 0.3rem;
    margin: 0 0.2rem;
}

.up>.upLeft>ul>li>input {
    height: 0.15rem;
    background: #fff;
    border: 1px solid #000;
    outline: skyblue;
}

.up>.upRight {
    width: 40%;
    height: 100%;
    background: #fff;
    display: flex;
    overflow: hidden;
}

.avatar-uploader>.ant-upload {
    width: 0.8rem;
    height: 1rem;
}

.down {
    width: 100%;
    height: 2rem;
    background: #fff;
}

.down>h2 {
    color: #000;
    font-size: 25px;
}

.down>ul {
    height: 1rem;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.down>ul>li {
    height: 0.16rem;

}

.down>ul>li>span {
    margin: 0 .1rem;
}

.down>ul>li>input {
    height: 100%;
    width: 1rem;
    outline: none;
}

.down>.btn {
    display: flex;
    justify-content: center;
}

.down>.btn>.s-btn {
    margin: 0 0.2rem;
}



